import React from "react";
import styled from "styled-components";
import { MapProvider, Map } from "react-tiled";

import compose from "modules/utils/compose";
import { KeyboardController, TouchController } from "modules/controller";
import { withSocket } from "modules/net";

import Player from "./Player";
import Camera from "./Camera";

const FPS = 60;

const GameInstanceWrapper = styled.div`
  height: 100vh;
  width: 100vw;
  background-color: #1c1117;
`;

const DebugLabel = styled.label`
  position: fixed;
  top: 0;
  left: 0;
`;

class GameInstance extends React.Component {
  static FOO = "bar";

  state = {
    debug:
      process.env.NODE_ENV === "development" &&
      JSON.parse(localStorage.getItem("bombersplash:debug") || "false"),
    map: null,
    players: [],
    gameId: null,
    playerId: null,
    inputVel: { x: 0, y: 0 }
    // playerState: {
    //   pos: { x: 0, y: 0 },
    //   rot: 0,
    //   vel: { x: 0, y: 0 }
    // }
  };

  frameInterval = null;
  lastFrameTimestamp = null;
  world = null;

  async componentDidMount() {
    const { socket } = this.props;

    await new Promise((resolve, reject) => {
      if (socket.connected) return resolve();
      const timeout = setTimeout(reject, 3000);
      socket.once("connect", () => {
        clearTimeout(timeout);
        resolve();
      });
    });

    socket.on("game:update", ({ players }) => {
      this.setState({ players });
    });

    socket.emit("game:join", (gameId, playerId, mapPath) => {
      this.setState({ gameId, playerId, mapPath });
    });

    this.playerUpdateInterval = setInterval(() => {
      socket.emit("player:update", { inputVel: this.state.inputVel });
    }, 1000 / FPS);
  }

  componentWillUnmount() {
    clearInterval(this.frameInterval);
    clearInterval(this.playerUpdateInterval);
  }

  handleMap = async map => {
    this.setState({ map });

    // const { BombersplashWorld } = await physicsLoader();
    // this.world = BombersplashWorld.new();
    // this.world.set_gravity(0, 0);
    //
    // getMapCollisionRects(map)
    //   .map(rect => ({
    //     x: rect.x + rect.width / 2,
    //     y: rect.y + rect.height / 2,
    //     width: rect.width / 2,
    //     height: rect.height / 2
    //   }))
    //   .forEach(({ x, y, width, height }) => {
    //     this.world.add_wall(x, y, width, height);
    //   });
    //
    // const startPosition = getMapStartPositions(map).green;
    // this.world.add_player(
    //   "sam",
    //   startPosition.x,
    //   startPosition.y,
    //   Player.COLLISION_BOX.width / 2,
    //   Player.COLLISION_BOX.height / 2
    // );
    //
    // this.setState({
    //   playerState: {
    //     pos: { ...startPosition },
    //     rot: 0,
    //     vel: { x: 0, y: 0 }
    //   }
    // });
    //
    // this.frameInterval = setInterval(this.draw, INTERVAL);
  };

  // draw = () => {
  //   const now = Date.now();
  //   if (this.lastFrameTimestamp) {
  //     const deltaTime = now - this.lastFrameTimestamp;
  //     this.handleFrame(deltaTime);
  //   }
  //
  //   this.lastFrameTimestamp = now;
  // };

  // handleFrame = deltaTime => {
  //   this.world.set_player_velocity(
  //     "sam",
  //     this.state.inputVel.x * Player.MAX_SPEED,
  //     this.state.inputVel.y * Player.MAX_SPEED
  //   );
  //
  //   this.world.set_timestep(deltaTime / 1000);
  //   this.world.step();
  //   const [posX, posY, rot, velX, velY] = this.world.get_player_state("sam");
  //
  //   this.setState({
  //     playerState: {
  //       pos: { x: posX, y: posY },
  //       rot,
  //       vel: { x: velX, y: velY }
  //     }
  //   });
  // };

  handleInputVelChange = inputVel => {
    this.setState({ inputVel });
  };

  handleInputAction = () => {};

  handleChangeDebug = evt => {
    this.setState({ debug: evt.target.checked });
    localStorage.setItem(
      "bombersplash:debug",
      JSON.stringify(evt.target.checked)
    );
  };

  render() {
    const { debug, map, mapPath, playerId, players, inputVel } = this.state;

    if (!mapPath) return null;

    const focusPoint = players.reduce(
      (acc, player) => (player.id === playerId ? player.pos : acc),
      { x: 0, y: 0 }
    );

    return (
      <MapProvider
        mapUrl={process.env.REACT_APP_GAME_HOST + mapPath}
        onMapLoaded={this.handleMap}
      >
        {map && (
          <GameInstanceWrapper>
            <Camera scale={3} focusPoint={focusPoint} debug={debug}>
              <Map debug={debug}>
                {players.map(player => (
                  <Player key={player.id} player={player} debug={debug} />
                ))}
              </Map>
            </Camera>

            <KeyboardController
              onInputVelChange={this.handleInputVelChange}
              onInputAction={this.handleInputAction}
              inputVel={inputVel}
            />

            <TouchController
              onInputVelChange={this.handleInputVelChange}
              onInputAction={this.handleInputAction}
              inputVel={inputVel}
            />

            {process.env.NODE_ENV === "development" && (
              <DebugLabel>
                <input
                  type="checkbox"
                  checked={debug}
                  onChange={this.handleChangeDebug}
                />
                Debug
              </DebugLabel>
            )}
          </GameInstanceWrapper>
        )}
      </MapProvider>
    );
  }
}

export default compose(withSocket())(GameInstance);



