import React from "react";

const anyPressed = pressedKeys => keys => {
  for (const key of Array.isArray(keys) ? keys : [keys]) {
    if (~pressedKeys.indexOf(key)) return true;
  }

  return false;
};

export default class KeyboardController extends React.PureComponent {
  pressedKeys = [];

  componentDidMount() {
    window.addEventListener("keydown", this.handleKeyDown);
    window.addEventListener("keyup", this.handleKeyUp);
  }

  componentWillUnmount() {
    window.removeEventListener("keydown", this.handleKeyDown);
    window.removeEventListener("keyup", this.handleKeyUp);
  }

  handleKeyDown = event => {
    const keys = [
      "ArrowUp",
      "ArrowDown",
      "ArrowLeft",
      "ArrowRight",
      "z",
      "q",
      "s",
      "d",
      " "
    ];
    if (~keys.indexOf(event.key)) event.preventDefault();

    const index = this.pressedKeys.indexOf(event.key);
    if (~index) return;

    if (event.key === " ") this.props.onInputAction();

    this.pressedKeys.push(event.key);
    this.handleChange();
  };

  handleKeyUp = event => {
    const index = this.pressedKeys.indexOf(event.key);
    if (~index) this.pressedKeys.splice(index, 1);
    this.handleChange();
  };

  handleChange() {
    const { onInputVelChange } = this.props;
    const inputVel = { x: 0, y: 0 };

    const testKeys = anyPressed(this.pressedKeys);

    if (testKeys(["ArrowUp", "z"])) inputVel.y -= 1;
    if (testKeys(["ArrowDown", "s"])) inputVel.y += 1;
    if (testKeys(["ArrowLeft", "q"])) inputVel.x -= 1;
    if (testKeys(["ArrowRight", "d"])) inputVel.x += 1;

    const hypot = Math.hypot(inputVel.x, inputVel.y);

    onInputVelChange({
      x: hypot < 1 ? inputVel.x : inputVel.x / hypot,
      y: hypot < 1 ? inputVel.y : inputVel.y / hypot
    });
  }

  render() {
    return null;
  }
}



