import React from "react";
import styled from "styled-components";

import Stick from "./Stick";

const TouchControllerStick = styled(Stick)`
  display: none;
  position: absolute;
  bottom: 2vw;
  left: 2vw;

  @media (hover: none) {
    display: block;
  }
`;

export default class TouchController extends React.PureComponent {
  render() {
    const { inputVel, onInputVelChange } = this.props;

    return (
      <TouchControllerStick value={inputVel} onChange={onInputVelChange} />
    );
  }
}



