import React from "react";
import styled from "styled-components";
import debounce from "lodash.debounce";

const CameraWrapper = styled.div`
  overflow: hidden;
  position: relative;
  height: 100%;
  width: 100%;
`;

const THRESHOLD_RATIO = 1 / 8;

export default class Camera extends React.PureComponent {
  state = {
    cameraSize: { width: 0, height: 0 },
    cameraPosX: Math.round(this.props.focusPoint.x),
    cameraPosY: Math.round(this.props.focusPoint.y)
  };

  componentDidMount() {
    window.addEventListener("resize", this.handleResize);
  }

  componentWillUnmount() {
    window.removeEventListener("resize", this.handleResize);
  }

  static getDerivedStateFromProps(
    { scale, focusPoint },
    { cameraSize, cameraPosX, cameraPosY }
  ) {
    const newState = {};

    const thresholdX = (cameraSize.width / 2 / scale) * THRESHOLD_RATIO;
    const thresholdY = (cameraSize.height / 2 / scale) * THRESHOLD_RATIO;
    const deltaX = focusPoint.x - cameraPosX;
    const deltaY = focusPoint.y - cameraPosY;

    if (Math.abs(deltaX) > thresholdX)
      newState.cameraPosX = Math.round(
        cameraPosX - Math.sign(deltaX) * (thresholdX - Math.abs(deltaX))
      );

    if (Math.abs(deltaY) > thresholdY)
      newState.cameraPosY = Math.round(
        cameraPosY - Math.sign(deltaY) * (thresholdY - Math.abs(deltaY))
      );

    return newState;
  }

  updateCameraSize = () => {
    if (!this.wrapperNode) return;
    const wrapperRect = this.wrapperNode.getBoundingClientRect();

    this.setState({
      cameraSize: { width: wrapperRect.width, height: wrapperRect.height }
    });
  };

  handleWrapperRef = ref => {
    this.wrapperNode = ref;
    this.updateCameraSize();
  };

  handleResize = debounce(this.updateCameraSize, 100);

  render() {
    const { scale, debug, children } = this.props;
    const { cameraSize, cameraPosX, cameraPosY } = this.state;

    const translateX = Math.round(cameraSize.width / 2 - cameraPosX * scale);
    const translateY = Math.round(cameraSize.height / 2 - cameraPosY * scale);

    return (
      <CameraWrapper innerRef={this.handleWrapperRef}>
        <div
          style={{
            position: "absolute",
            transform: `
              translate(
                ${translateX}px,
                ${translateY}px
              )
              scale(${scale})
            `,
            transformOrigin: "top left"
          }}
        >
          {children}
        </div>

        {debug && (
          <div
            style={{
              position: "absolute",
              top: "50%",
              left: "50%",
              transform: "translate(-50%, -50%)",
              width: cameraSize.width * THRESHOLD_RATIO,
              height: cameraSize.height * THRESHOLD_RATIO,
              outline: "1px solid rgba(255, 0, 0, .5)"
            }}
          />
        )}
      </CameraWrapper>
    );
  }
}



