import React from "react";
import { keyframes } from "styled-components";
import { Tile, withMap, utils } from "react-tiled";

import compose from "modules/utils/compose";

const runAnimation = keyframes`
  0% {
    transform: translateY(-20%) rotate(0deg);
  }
  25% {
    transform: translateY(-10%) rotate(-5deg);
  }
  50% {
    transform: translateY(-20%) rotate(0deg);
  }
  75% {
    transform: translateY(-10%) rotate(5deg);
  }
  100% {
    transform: translateY(-20%) rotate(0deg);
  }
`;

const SPEED_THRESHOLD = 0.1;

class Player extends React.PureComponent {
  static MAX_SPEED = 8 * 16; // tiles * tileSize / seconds
  static WIDTH = 16;
  static HEIGHT = 16;

  static COLLISION_BOX = {
    width: 8,
    height: 10
  };

  static getTeamGidOffset(team) {
    return (
      {
        yellow: 0,
        green: 1,
        blue: 2,
        red: 3
      }[team] || 0
    );
  }

  render() {
    const { map, player, debug, children } = this.props;

    const speed = Math.hypot(player.vel.x, player.vel.y);

    this.scale =
      (Math.abs(player.vel.x) > SPEED_THRESHOLD
        ? Math.sign(player.vel.x)
        : 0) ||
      this.scale ||
      1;

    return (
      <div
        style={{
          position: "absolute",
          top: player.pos.y - Player.HEIGHT / 2,
          left: player.pos.x - Player.WIDTH / 2,
          width: Player.WIDTH,
          height: Player.HEIGHT,
          transform: `scaleX(${this.scale}) rotate(${player.rot.toFixed(
            3
          )}rad)`,
          zIndex: Math.round(player.pos.y)
        }}
      >
        <div
          style={{
            width: Player.WIDTH,
            height: Player.HEIGHT,
            animation: `${runAnimation} 0.4s infinite`,
            animationPlayState: speed > SPEED_THRESHOLD ? "running" : "paused",
            transformOrigin: `${Player.WIDTH / 2}px ${Player.HEIGHT}px`
          }}
        >
          <Tile
            pos={{ x: 0, y: -Player.HEIGHT }}
            tileGid={
              utils.getTileGid(map.tilesets, "Custom", 56) +
              Player.getTeamGidOffset(player.team)
            }
          />
          <Tile
            pos={{ x: 0, y: 0 }}
            tileGid={
              utils.getTileGid(map.tilesets, "Custom", 72) +
              Player.getTeamGidOffset(player.team)
            }
          />

          {children}
        </div>

        {debug && (
          <div
            style={{
              position: "absolute",
              top: "50%",
              left: "50%",
              width: Player.COLLISION_BOX.width,
              height: Player.COLLISION_BOX.height,
              transform: `translate(-50%, -50%)`,
              backgroundColor: "rgba(255, 0, 0, .5)"
            }}
          />
        )}
      </div>
    );
  }
}

export default compose(withMap)(Player);



